
.Favorites {
    padding-top: 60px;
    padding-bottom: 60px;
    @include tablet {
      padding-top: 100px;
      padding-bottom: 100px;
    }
    & img {
        display: block;
        max-width: 100%;
        height: auto;
        width: 100%;
      }
  }
  
  
  .Favorites__titel {
    @include fonts(700, 28px, calc(32 / 28));
    text-align: center;
    margin-bottom: 20px;
    color: #2c2c2c;
    @include tablet {
      font-weight: 700;
      font-size: 42px;
      line-height: calc(49 / 42);
    }
  }
  
  .Favorites__text {
    @include fonts(null, 14px, calc(16 / 14));
    margin-bottom: 60px;
    color: #2c2c2c;
    @include tablet {
        @include fonts(null, 16px, calc(19 / 16));
  }
    @include desktop {
      margin-bottom: 40px;
    }
  }
  
  .Favorites__list {
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    @include tablet {
      gap: 32px;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;
    }
  }
  
  
  .Favorites__item {
    width: 100%;
    height: auto;
    padding: 17px;
    border: 11px solid #ffe550;
    border-radius: 50%;
    @include tablet {
      width: calc((100% - 32px) / 2);
      border: 14px solid #ffe550;
      padding: 21px;
    }
    @include desktop {
      padding: 24px;
      width: calc((100% - 64px) / 3);
      border: 16px solid #ffe550;
    }
    &:nth-last-child(-n + 3) {
      display: none;
      @include tablet {
        display: block;
      }
    }
  }
  